import { useQuery } from '@tanstack/react-query';
import { DataUtil } from '@youfoundation/transit-lib';
import { useEffect, useState } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { createAuthenticationProvider } from '../../provider/AuthenticationProvider';

const SHARED_SECRET = 'SS';
const LOGIN_PATH = '/owner/login';
const LOGIN_YOUAUTH_PATH = '/owner/login/youauth';

const RETURN_URL_PARAM = 'returnUrl';
const HOME_PATH = '/owner';

const useAuth = () => {
  const [authenticationState, setAuthenticationState] = useState<
    'unknown' | 'anonymous' | 'authenticated'
  >('unknown');
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();

  const checkToken = () => {
    const fetchData = async () => {
      const client = createAuthenticationProvider();
      return await client.hasValidToken();
    };
    return useQuery(['verifyToken'], fetchData, {
      refetchOnMount: false,
    });
  };

  const { data: hasValidToken, isFetchedAfterMount } = checkToken();

  const authenticate = async (password: string) => {
    const client = createAuthenticationProvider();
    const response = await client.authenticate(password);

    if (!response) {
      return false;
    }

    window.localStorage.setItem(SHARED_SECRET, DataUtil.uint8ArrayToBase64(response.sharedSecret));
    setAuthenticationState('authenticated');

    doRedirectToReturn();

    return true;
  };

  const forceSetPassword_temp = async (newPassword: string): Promise<boolean> => {
    const client = createAuthenticationProvider();
    return client.forceSetPassword_temp(newPassword);
  };

  const createHomeToken = async (returnUrl: string): Promise<boolean> => {
    const client = createAuthenticationProvider();
    return client.createHomeToken(returnUrl);
  };

  const logout = async () => {
    const client = createAuthenticationProvider();
    await client.logout();
    setAuthenticationState('anonymous');

    window.localStorage.removeItem(SHARED_SECRET);
    window.location.href = LOGIN_PATH;
  };

  const getSharedSecret = () => {
    const raw = window.localStorage.getItem(SHARED_SECRET);
    if (raw) {
      return DataUtil.base64ToUint8Array(raw);
    }
  };

  const doRedirectToReturn = () => {
    if (window.location.pathname === LOGIN_PATH) {
      const returnUrl = searchParams.get(RETURN_URL_PARAM);
      if (returnUrl) {
        navigate(decodeURIComponent(returnUrl));
      } else {
        navigate(HOME_PATH);
      }
    } else if (window.location.pathname === LOGIN_YOUAUTH_PATH) {
      const returnUrl = searchParams.get(RETURN_URL_PARAM);
      if (returnUrl) {
        createHomeToken(decodeURIComponent(returnUrl));
      } else {
        navigate(HOME_PATH);
      }
    }
  };

  useEffect(() => {
    if (isFetchedAfterMount && hasValidToken !== undefined) {
      setAuthenticationState(hasValidToken ? 'authenticated' : 'anonymous');

      if (hasValidToken) {
        // When authenticated check if on Login Pages and if so redirects to return or Home
        doRedirectToReturn();
      } else if (
        window.location.pathname !== LOGIN_PATH &&
        window.location.pathname !== LOGIN_YOUAUTH_PATH
      ) {
        // When not authenticated and not on either of the Login Pages on the Owner app => redirect to regular login
        window.location.href = `${LOGIN_PATH}?returnUrl=${encodeURIComponent(
          window.location.pathname + window.location.search
        )}`;
      }
    }
  }, [hasValidToken]);

  return {
    authenticate,
    createHomeToken,
    forceSetPassword_temp,
    logout,
    getSharedSecret,
    isAuthenticated: authenticationState !== 'anonymous',
  };
};

export default useAuth;
