import { useQuery } from '@tanstack/react-query';
import { ApiType, Client } from '@youfoundation/transit-lib';
import { useRef } from 'react';
import { CircleProvider } from '../../provider/connections/CircleProvider';
import useAuth from '../auth/useAuth';

const useEnsureDrives = () => {
  const { getSharedSecret, isAuthenticated } = useAuth();
  const driveState = useRef('unknown');

  const ensureDrives = async () => {
    if (!isAuthenticated) {
      return;
    }

    if (driveState.current === 'ensured' || driveState.current === 'pending') {
      return;
    }

    driveState.current = 'pending';
    console.log('Ensure drives');

    const client = new Client({ api: ApiType.Owner, sharedSecret: getSharedSecret() });

    try {
      await client.homePageProvider.ensureConfiguration();
      await client.profileDefinitionProvider.ensureConfiguration();
      await client.profileDataProvider.ensureConfiguration();
      await client.blogDefinitionProvider.ensureConfiguration();
    } catch (ex) {
      console.error('Ensure drives failed', ex);
      driveState.current = 'failed';
    }

    driveState.current = 'ensured';
  };

  return useQuery(['ensureDrives'], ensureDrives, {
    refetchOnMount: false,
  });
};

export default useEnsureDrives;
