import { FC, ReactNode } from 'react';
import { useSearchParams } from 'react-router-dom';
import useEnsureDrives from '../../hooks/ensureDrives/useEnsureDrives';
import useDarkMode from '../../hooks/useDarkMode';
import Sidenav from './Sidenav/Sidenav';
import UrlNotifier from './UrlNotifier/UrlNotifier';

interface LayoutProps {
  children?: ReactNode;
}

const Layout: FC<LayoutProps> = ({ children }) => {
  const [searchParams] = useSearchParams();
  const uiSetting = searchParams.get('ui');

  useEnsureDrives();

  if (uiSetting === 'minimal' || uiSetting === 'focus') {
    return (
      <MinimalLayout>
        {uiSetting !== 'focus' ? <UrlNotifier /> : null}
        <div className="py-4 px-5 sm:py-8 sm:px-10">{children}</div>
      </MinimalLayout>
    );
  }

  return (
    <div className="flex flex-row dark:text-gray-400">
      <Sidenav />
      <div className={`flex min-h-screen w-full flex-col dark:bg-gray-900 dark:text-gray-400`}>
        <div className="min-h-full py-8 px-10">{children}</div>
      </div>
    </div>
  );
};

export const MinimalLayout: FC<LayoutProps> = ({ children }) => {
  useDarkMode();
  return <div className="min-h-screen dark:bg-gray-900 dark:text-gray-400">{children}</div>;
};

export default Layout;
